*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ul,li{
  list-style: none;
}
a{
  color: inherit;
  text-decoration: none;
}
@font-face {
	font-family: 'Pretendard Variable';
	font-weight: 45 920;
	font-style: normal;
	font-display: swap;
	src: url('./woff2/PretendardVariable.woff2') format('woff2-variations');
}

button[type="submit"],
input{
	font-family: 'Pretendard Variable';
}
:root{
	font-family: 'Pretendard Variable';
  font-size: 62.5%;

  --grayscale-900: #212121 ;
  --grayscale-700: #343434 ;
  --grayscale-600: #666666 ;
  --grayscale-500: #A5A5A5 ;
  --grayscale-400: #C4C4C4 ;
  --grayscale-300: #EEEEEE ;
  --grayscale-200: #F1F1F1 ;
  --grayscale-100: #F7F7F7 ;

  --primary-700 : #6B5FFF;
  --primary-200 : #EAE9FF;
  --primary-100 : #F8F7FF;

  --secondary-yellow : #FBB750;
  --secondary-red : #ED3B66;
  --secondary-navy : #0F3472;

  --gradient_01: linear-gradient(269.69deg, #FF85C0 10.24%, #D488F7 34.58%, #76F7FF 108.15%);
  ;
}
.headline01{
  font-size: 2.8rem;
  font-weight: 700;
  line-height: 140%;
  letter-spacing: -2%;
}
.headline02{
  font-size: 2.4rem;
  font-weight: 700;
  line-height: 140%;
  letter-spacing: -2%;
}
.sub_headline01{
  font-size: 2.2rem;
  font-weight: 700;
  line-height: 140%;
  letter-spacing: -4%;
}
.sub_headline02{
  font-size: 2rem;
  font-weight: 700;
  line-height: 140%;
  letter-spacing: -4%;
}
.large01{
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 140%;
  letter-spacing: -1%;
}
.large02{
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 140%;
  letter-spacing: -2%;
}
.large03{
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -2%;
}
.small01{
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 150%;
  letter-spacing: -2%;
}
.small02{
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -2%;
}
.label01{
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 150%;
  letter-spacing: -4%;
}
.label02{
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 150%;
  letter-spacing: -4%;
}
.caption{
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 140%;
  letter-spacing: -2%;
}

.flex{
  display: flex;
}
.al_ce{
  align-items: center;
}
.al_fs{
  align-items: flex-start;
}
.al_fe{
  align-items: flex-end;
}
.ju_sb{
  justify-content: space-between;
}
.ju_sa{
  justify-content: space-around;
}
.primary_btn{
  display: block;
  color: var(--primary-700);
  border: 1px solid var(--primary-700);
  border-radius: 1.2rem;
  width: 100%;
  text-align: center;
  padding: 1rem;
}
.primary_fill_btn{
  display: block;
  color: #fff;
  background-color: var(--primary-700);
  border: 1px solid var(--primary-700);
  border-radius: 1.2rem;
  width: 100%;
  text-align: center;
  padding: 1rem;
}
.gray_btn{
  display: block;
  color: #fff;
  background-color: var(--grayscale-400);
  border: 1px solid var(--grayscale-400);
  border-radius: 1.2rem;
  width: 100%;
  text-align: center;
  padding: 1rem;
}
button.gray_btn{
  color: #fff;
  background-color: var(--grayscale-400);
  border: none;
  padding: 2rem;
  border-radius: 1.2rem;
  text-align: center;
}
button.primary_btn{
  color: #fff;
  background-color: var(--primary-700);
  font-weight: 700;
  text-align: center;
}
/* form_type_01 */
.form_type_01 .lable_box > label{
  display: block;
  margin-bottom: 1.4rem;
}
.form_type_01 input::placeholder{
  color: var(--grayscale-400);
}
.form_type_01 input,
.form_type_01 .radio > label{
  display: block;
  width: 100%;
  font-size: 1.6rem;
  padding: 1.6rem;
  border: 1px solid var(--grayscale-400) ;
  border-radius: 1.2rem;
  position: relative;
}
.form_type_01 .radio {
  position: relative;
  width: 100%;
  text-align: center;
}
.form_type_01 .radio > input[type="radio"]{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  opacity: 0;
}
.form_type_01 .radio > input[type="radio"]:checked + label{
  color: var(--primary-700);
  border: 1px solid var(--primary-700);
}
/* custom */
.App{
  min-width: 360px;
  min-height: 100vh;
  margin: 0 auto;
}
@media screen and (max-width:1920px) {
  :root{
    font-size: 2.7778vw;
  }
}
@media screen and (max-width:360px) {
  :root{
    font-size: 10px;
  }
}
.App .con{
  padding-right: 2rem;
  padding-left: 2rem;
}

.main_visual{
  position: relative;
  margin-bottom: 15.4rem ;
}

.main_visual .visual_img{
}
.main_visual .visual_img > img{
  width: 100%;
}

.visual_text{
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  text-align: center;
  color: var(--secondary-navy);
}
.visual_text > .headline01{
  margin-bottom: 3rem;
}
.visual_text > .small02{
  margin-bottom:4.4rem;
}
.btn_gradient > img{
  width: 27.4rem;
  display: block;
  margin: 0 auto;
}
.scroll_nav{
  text-align: center;
  color: var(--grayscale-500);
  margin-bottom: 0.9rem;
  padding-bottom: 9rem;
}
.scroll_arrow{
  width: 3.6rem;
}
.about_wrap{
  border-top: 1px solid var(--grayscale-200);
  padding-top: 3rem;
  border-bottom: 1px solid var(--grayscale-200);
  padding-bottom: 5rem;
}
.about_wrap .text_box{
  padding: 2.4rem 2rem ;
  background: var(--primary-100);
  border-radius: 1.2rem;
  margin-bottom: 2rem;
}
.about_wrap .text_box > .sub_headline02{
  color: var(--grayscale-700);
  margin-bottom: 2rem;
}
.about_wrap .text_box > .small02{
  color: var(--grayscale-600);
}
.survey_wrap{
  border-bottom: 1px solid var(--grayscale-200);
}
.camera_slide_wrap .slide_img{
  display: block;
  margin: 0 auto;
  width: 30.4rem;
}
.survey_wrap .slide_img{
  display: block;
  margin: 0 auto;
  width: 20rem;
}
.survey_box{
  padding-top: 4rem;
  padding-bottom: 5.4rem;
}
.survey_wrap .box_label{
  color: var(--primary-700);
  background-color:var(--primary-200);
  padding: 0.9rem;
  display: inline-block;
  border-radius: 0.5rem ;
  margin-bottom: 2.4rem;
}
.survey_wrap .sub_headline01{
  margin-bottom: 2.6rem;
}
.survey_wrap .sub_headline01 span{
  color: var(--primary-700);
  position: relative;
}
.survey_wrap .sub_headline01 span::after{
  content: "";
  height: 1px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: var(--primary-700);
}
.survey_wrap .primary_fill_btn{
  margin-top: 7rem;
}
.survey_wrap .slick-dots li{
  width: 0.6rem;
  height: 0.6rem;
}
.camera_slide_wrap .slick-dots li button:before,
.survey_wrap .slick-dots li button:before{
  opacity: 1;
  color: var(--grayscale-300);
}
.camera_slide_wrap .slick-dots li.slick-active button:before,
.survey_wrap .slick-dots li.slick-active button:before{
  color: var(--primary-700);
}
.survey_wrap .caption{
  margin-top: 2.4rem;
  color: var(--grayscale-500);
}
.share_wrap{
  padding-top: 4rem;
  padding-bottom: 4rem;
  color: var(--grayscale-500);
  text-align: center;
}
.share_icon_wrap{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.6rem;
  margin-top: 3rem;
}
.share_icon_wrap img{
  width: 5.6rem;
}
.sub_page{
  height: 100vh;
  overflow: hidden;
}
.sub_page .sub_top_bar{
  text-align: center;
  height: 5.6rem ;
  display: flex;
  position: relative;
  align-items: center;
  padding-left: 2rem;
  padding-right: 2rem;
}
.sub_page .sub_top_bar .sub_page_top_title{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.sub_page .sub_top_bar .back_btn{
  width: 2.4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.sub_page .sub_body_wrap{
  overflow: scroll;
  height: calc(100vh - 5.6rem);
}
.sub_page .sub_body_wrap .sub_body{
  min-height: 100%;
  height: auto;
  padding-top: 2rem;
}
.sub_page .sub_body_wrap .sub_body .sub_title .sub_headline02{
  color: var(--grayscale-700);
  margin-bottom: 1rem;
}
.sub_page .sub_body_wrap .sub_body .sub_title .small02{
  color: var(--grayscale-600);
}
.info_wrap{
  margin-top: 4rem;
}
.info_wrap > form{
  display: flex;
  gap: 3rem;
  flex-direction: column;
}
.radio_wrap {
  gap: 1.6rem ;
}
.check {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
}
.check > input{
  width: auto;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0;
}
.check label{
  display: flex !important;
  margin-bottom: 1.4rem;
  margin-top: 1.4rem;
}
.check label .icon_box img:nth-child(1){
  display: block;
}
.check label .icon_box img:nth-child(2){
  display: none;
}
.check input:checked + label .icon_box  img:nth-child(1){
  display: none;
}
.check input:checked + label .icon_box  img:nth-child(2){
  display: block;
}
.check .icon_box{
  margin-right: 1.3rem ;
}
.all_agree{
  border-bottom: 1px solid var(--grayscale-300);
}
input[type="date"]::placeholder{
  content: attr(placeholder);
}
.check .arrow_box{
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.camera_slide_wrap .slick-slider {
  margin-bottom: 9.6rem;
} 
.camera_slide_wrap .btn_wrap > div{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
.camera_slide_wrap .btn_wrap > div:first-child{
  display: flex;
}
.camera_slide_wrap .btn_wrap.active > div:first-child{
  display: none;
}
.camera_slide_wrap .btn_wrap > div:last-child{
  display: none;
}
.camera_slide_wrap .btn_wrap.active > div:last-child{
  display: flex;
}
.progress_box{
  width: 20rem;
  height: 0.6rem;
  background-color: var(--grayscale-200);
  border-radius: 0.3rem;
  margin: 0 auto;
}
.progress_bar{
  display: block;
  min-width: 0.6rem;
  height: 100%;
  background-color: var(--primary-700);
  border-radius: 0.3rem;
  transition: 0.5s;
}
.survey_form_wrap{
  margin-top: 7.4rem;
  margin-bottom: 11rem;
}
.survey_form_wrap .survey_num{
  color: var(--grayscale-500);
  text-align: center;
  margin-bottom: 4rem;
}
.survey_form_wrap .survey_title{
  text-align: center;
  margin-bottom: 5rem ;
}
.survey_form_wrap .survey_radio{
  position: relative;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.survey_form_wrap .survey_radio input{
  position: absolute;
  right: 2rem;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0;
}
.survey_form_wrap .survey_radio label{
  display: flex;
  justify-content: space-between;
  padding: 2rem;
  border: 1px solid var(--grayscale-300);
  border-radius: 1.2rem;
}
.survey_form_wrap .survey_radio input + label .check_box{
  width: 1.8rem;
  height: 1.8rem;
  border-radius: 0.9rem;
  border: 1px solid var(--grayscale-400);
} 
.survey_form_wrap .survey_radio input:checked + label {
  background-color: var(--primary-100);
  border: 1px solid var(--primary-700);
}
.survey_form_wrap .survey_radio input:checked + label .check_box{
  width: 1.8rem;
  height: 1.8rem;
  border-radius: 0.9rem;
  border: 1px solid var(--primary-700);
  position: relative;
} 
.survey_form_wrap .survey_radio input:checked + label .check_box::after{
  content: "";
  display: block;
  position: absolute;
  width: 1rem;
  height: 1rem;
  border-radius: 0.5rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color:  var(--primary-700);
} 